export default "@keyframes flash {\n  from {\n    background-color: #777;\n  }\n  to {\n    background-color: #22c55e;\n  }\n}\n\n/* Code Examples */\n\n/* Code Examples */\n.code-example {\n    background: #1a1a1a;\n    /*width: 500px;*/\n    /*height: 500px;*/\n    border: 1px solid #2a2a2a;\n    border-radius: 8px;\n    overflow: hidden;\n    margin-bottom: 1.5rem;\n}\n.code-example {\n    overflow-x: auto;\n}\n\n.code-example svg {\n    width: 100%;\n    height: auto;\n    max-width: none;\n}\n\n.code-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    background: #2a2a2a;\n    padding: 0.2rem 0.2rem 0.2rem 1rem;\n    border-bottom: 1px solid #3a3a3a;\n}\n\n.code-title {\n    font-size: 0.875rem;\n    font-weight: 500;\n    color: #e5e5e5;\n}\n\n.code-example pre {\n    padding: 1.5rem;\n    margin: 0;\n    overflow-x: auto;\n    background: transparent;\n}\n\n.code-example code {\n    color: #e5e5e5;\n    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\n    font-size: 0.875rem;\n    line-height: 1.6;\n}\n\n\n.code-copy-btn {\n    background: #555;\n    color: white;\n    border: none;\n    padding: 2px 8px;\n    border-radius: 4px;\n    cursor: pointer;\n    transition: background-color 0.3s ease, transform 0.1s ease;\n}\n\n.code-copy-btn:active {\n    transform: scale(0.95);\n}\n\n.code-copy-btn.copied {\n    background-color: #22c55e;\n    animation: flash 0.4s ease-out;\n}\n\n\n\n.actor {\n    stroke: #ccccff;\n    fill: #ececff;\n}\ntext.actor {\n    fill: black;\n    stroke: none;\n    font-family: Helvetica;\n}\n\n.actor-line {\n    stroke: #c3c3c3;\n}\n\n.messageLine0 {\n    stroke-width: 1.5;\n    stroke-dasharray: '2 2';\n    marker-end: 'url(#arrowhead)';\n    stroke: #d1d1d1;\n}\n\n.messageLine1 {\n    stroke-width: 1.5;\n    stroke-dasharray: '2 2';\n    stroke: #e4e4e4;\n}\n\n#arrowhead {\n    fill: #b3b3b3;\n}\n\n.messageText {\n    fill: #d3d3d3;\n    stroke: none;\n    font-family: 'trebuchet ms', verdana, arial;\n    font-size: 14px;\n}\n\n.labelBox {\n    stroke: #ccccff;\n    fill: #ececff;\n}\n\n.labelText {\n    fill: #d7d7d7;\n    stroke: none;\n    font-family: 'trebuchet ms', verdana, arial;\n}\n\n.loopText {\n    fill: #c5c5c5;\n    stroke: none;\n    font-family: 'trebuchet ms', verdana, arial;\n}\n\n.loopLine {\n    stroke-width: 2;\n    stroke-dasharray: '2 2';\n    marker-end: 'url(#arrowhead)';\n    stroke: #ccccff;\n}\n\n.note {\n    stroke: #decc93;\n    fill: #fff5ad;\n}\n\n.noteText {\n    fill: #c8c8c8;\n    stroke: none;\n    font-family: 'trebuchet ms', verdana, arial;\n    font-size: 14px;\n}\n\n/*.mermaid .customEdge path {*/\n/*    stroke: #ff0000; !* 라인 색상 *!*/\n/*    fill: #ff0000; !* 화살표 채우기 색상 *!*/\n/*}*/\n/*.mermaid .customEdge marker path {*/\n/*    fill: #ff0000; !* 화살표 마커의 채우기 색상 *!*/\n/*}*/\n/*.link, .link path,*/\n/*.edgePath path {*/\n/*    stroke: #b10606 !important;  !* 🔸 원하는 색상 *!*/\n/*    stroke-width: 2px !important;*/\n/*}*/\n@media (max-width: 768px) {\n    .code-example pre {\n        padding: 1rem;\n        overflow-x: auto;\n        font-size: 0.8rem;\n    }\n}";