export default ".movie-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n  gap: 20px;\n  padding: 20px;\n}\n\n.movie-card {\n  position: relative;\n  border-radius: 8px;\n  overflow: hidden;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n}\n\n.movie-poster-link {\n  display: block;\n  text-decoration: none;\n  color: inherit;\n}\n\n.movie-card:hover {\n  transform: translateY(-5px);\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n}\n\n.movie-poster {\n  position: relative;\n  width: 100%;\n  aspect-ratio: 2/3;\n}\n\n.movie-poster img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  display: block;\n}\n\n.movie-overlay {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));\n  color: white;\n  padding: 20px 15px 15px;\n  transform: translateY(100%);\n  transition: transform 0.3s ease;\n}\n\n.movie-card:hover .movie-overlay {\n  transform: translateY(0);\n}\n\n.movie-title {\n  margin: 0 0 10px 0;\n  font-size: 14px;\n  font-weight: 600;\n  line-height: 1.3;\n}\n\n.movie-actions {\n  display: flex;\n  justify-content: flex-end;\n}\n\n.external-link {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  width: 32px;\n  height: 32px;\n  background: rgba(255, 255, 255, 0.2);\n  border-radius: 50%;\n  color: white;\n  text-decoration: none;\n  transition: background-color 0.3s ease;\n}\n\n.external-link:hover {\n  background: rgba(255, 255, 255, 0.3);\n}\n\n.external-link i {\n  font-size: 14px;\n}\n\n/* Responsive breakpoints */\n@media (max-width: 768px) {\n  .movie-grid {\n    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));\n    gap: 15px;\n    padding: 15px;\n  }\n  \n  .movie-title {\n    font-size: 12px;\n  }\n}\n\n@media (max-width: 480px) {\n  .movie-grid {\n    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));\n    gap: 10px;\n    padding: 10px;\n  }\n  \n  .movie-overlay {\n    padding: 15px 10px 10px;\n  }\n  \n  .movie-title {\n    font-size: 11px;\n  }\n  \n  .external-link {\n    width: 28px;\n    height: 28px;\n  }\n  \n  .external-link i {\n    font-size: 12px;\n  }\n}";