export default "@keyframes flash {\n  from {\n    background-color: #777;\n  }\n  to {\n    background-color: #0d5126;\n  }\n}\n\n/* Code Snippet Container - 기존 code-example 스타일 기반 */\n.code-snippet-container {\n    background: #1a1a1a;\n    border: 1px solid #2a2a2a;\n    border-radius: 8px;\n    overflow: hidden;\n    /*margin-bottom: 1.5rem;*/\n}\n\n.code-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    background: #2a2a2a;\n    padding: 0;\n    border-bottom: 1px solid #3a3a3a;\n}\n\n.code-copy-btn {\n    background: #575757;\n    color: #d8d8d8;\n    border: none;\n    padding: 2px 8px;\n    border-radius: 4px;\n    cursor: pointer;\n    transition: background-color 0.3s ease, transform 0.1s ease;\n    margin: 0.3rem;\n    flex-shrink: 0;\n}\n\n.code-copy-btn:active {\n    transform: scale(0.95);\n}\n\n.code-copy-btn.copied {\n    background-color: #0d5126;\n    animation: flash 0.4s ease-out;\n}\n\n/* 탭 스타일 - 헤더 안에 배치 */\n.code-tabs {\n    display: flex;\n    background: transparent;\n    overflow-x: auto;\n    flex: 1;\n}\n\n.tab-btn {\n    background: #2a2a2a;\n    color: #9ca3af;\n    border: none;\n    padding: 0.5rem 0.8rem;\n    cursor: pointer;\n    border-right: 1px solid #222;\n    font-size: 0.875rem;\n    font-family: inherit;\n    transition: all 0.2s ease;\n    border-bottom: 3px solid transparent;\n    white-space: nowrap;\n    min-width: fit-content;\n}\n\n.tab-btn:hover {\n    background: #3a3a3a;\n    color: #e5e5e5;\n}\n\n.tab-btn.active {\n    background: #1a1a1a;\n    color: #e5e5e5;\n    border-bottom-color: #0366d6;\n    font-weight: 500;\n}\n\n.code-content {\n    position: relative;\n    background: #1a1a1a;\n}\n\n@media (max-width: 768px) {\n    .code-tabs {\n        overflow-x: auto;\n        scrollbar-width: thin;\n    }\n    \n    .tab-btn {\n        padding: 0.5rem 0.75rem;\n        font-size: 0.8rem;\n    }\n}";